.body {
  margin-top: 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.body > div {
  padding-bottom: 2%;
  padding: 3%;
  height: 20%;
  width: 15%;
  color: #fff;
}
.body > div > h1 {
  letter-spacing: 3px;
  padding: 1%;
}

.confirmed {
  background-color: grey;
}

.recovered {
  background-color: green;
}

.deaths {
  background-color: red;
}

@media screen and (max-width: 480px) {
  .body {
    margin-top: 20%;
  }
  .body > div {
    width: 70%;
    padding: 5%;
  }
  .select-country {
    margin-top: 15%;
  }
}
